import React from "react";
import { connect } from "react-redux";
import {getUsersThunk} from './../../redux/cabinet-reducer';
import Cabinet from "./Cabinet";

class CabinetContainer extends React.Component {
    constructor(props){
        super(props);
    }
    componentDidMount(){
        this.props.getUsersThunk();
    }
    render() {
        return (
            <Cabinet {...this.props} />
            
        )
    }
}
const mapStateToProps = (state) => {
    return {
        users: state.cabinetPage.users
    }
}
export default connect(mapStateToProps,{getUsersThunk})(CabinetContainer);